import {
    Container,
    SimpleGrid,
    Image,
    Flex,
    Heading,
    Text,
    Stack,
    StackDivider,
    Icon,
    useColorModeValue,
  } from '@chakra-ui/react';
  import { ReactElement } from 'react';
import TextButton from './TextButton';
  
  const Feature = ({ text, icon, iconBg }) => {
    return (
      <Stack direction={'row'} align={'center'}>
        <Flex
          w={8}
          h={8}
          align={'center'}
          justify={'center'}
          rounded={'full'}
          bg={iconBg}>
          {icon}
        </Flex>
        <Text fontWeight={600}>{text}</Text>
      </Stack>
    );
  };
  
  export default function About() {
    return (
      <Container maxW={'5xl'} py={12}>
        <SimpleGrid marginBottom={"100px"} columns={{ base: 1, md: 2 }} spacing={10}>
          <Stack marginTop={"150px"} spacing={4}>
            <Heading>Hello There...!!!</Heading>
            <Text  color={'black.500'} fontSize={'30px'}>
              I am Raghuveer Sain
            </Text>
            <Text color={'blue.500'} fontSize={'30px'}>
             "Full Stack Web Developer"
            </Text>
          </Stack>
          <Flex>
            <Image
              borderRadius={"250px"}
              alt={'feature image'}
              src={
                'https://avatars.githubusercontent.com/u/112657812?v=4'
              }
              objectFit={'cover'}
            />
          </Flex>
        </SimpleGrid>
        <TextButton text={"About Me"}/>
            <Text margin={"auto"} width={"60%"}  color={'black.500'} fontSize={'20px'}>
            Analytical and detail-oriented aspiring Full Stack Web Developer. Capable of writing production-ready code using MERN stack. Passionate about coding and intensely interested in working in a product-based company.
            </Text>
        <TextButton text={"Skills"}/>
      </Container>
    );
  }