import { Text, useColorModeValue } from '@chakra-ui/react'
import React from 'react'

const TextButton = ({text}) => {
  return (
    <div>
        <Text
              textTransform={'uppercase'}
              color={'blue.400'}
              fontWeight={600}
              fontSize={'sm'}
              bg={useColorModeValue('blue.50', 'blue.900')}
              p={3}
              alignSelf={'flex-start'}
              rounded={'md'}
              width={"20%"}
              margin={"auto"}
              >
              {text}
            </Text>
    </div>
  )
}

export default TextButton